.graphdatepicker {
    width: 100%;
    float: left;
    clear: both;
    display: inherit;
    background: #fff;
    padding: 12px 15px 12px 15px;
    margin: 0px 15px 25px 15px;
    border-radius: .25rem;
    box-shadow: 3px 0 10px 0 rgba(183, 192, 206, .2);
}


@media (max-width: 1024px) {
    .sm-custom-spacing {
        margin-left: 12px;
    }
}